'use client';
import Link from 'next/link';
import { useState } from 'react';

export default function Navbar() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <nav className="bg-gradient-to-r from-gray-900 to-gray-800 text-white shadow-lg">
      <div className="container mx-auto px-4 py-3">
        <div className="flex justify-between items-center">
          <Link href="/" className="text-2xl font-bold hover:text-blue-400 transition-colors">
            L3X4
          </Link>
          
          {/* Mobil Menü Butonu */}
          <button 
            className="md:hidden p-2"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              {isMenuOpen ? (
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              ) : (
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              )}
            </svg>
          </button>

          {/* Masaüstü Menü */}
          <div className="hidden md:flex space-x-6">
            <Link href="/" className="hover:text-blue-400 transition-colors">
              Ana Sayfa
            </Link>
            <Link href="/products" className="hover:text-blue-400 transition-colors">
              Ürünler
            </Link>
            <Link href="/about" className="hover:text-blue-400 transition-colors">
              Hakkımızda
            </Link>
            <Link href="/contact" className="hover:text-blue-400 transition-colors">
              İletişim
            </Link>
          </div>
        </div>

        {/* Mobil Menü */}
        {isMenuOpen && (
          <div className="md:hidden mt-4 space-y-3">
            <Link href="/" className="block hover:text-blue-400 transition-colors">
              Ana Sayfa
            </Link>
            <Link href="/products" className="block hover:text-blue-400 transition-colors">
              Ürünler
            </Link>
            <Link href="/about" className="block hover:text-blue-400 transition-colors">
              Hakkımızda
            </Link>
            <Link href="/contact" className="block hover:text-blue-400 transition-colors">
              İletişim
            </Link>
          </div>
        )}
      </div>
    </nav>
  );
}